h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: #2a2a2a;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

input {
  font-family: inherit;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

body {
  color: #2a2a2a;
  background-color: #fafafa;
  font-family: Helvetica Neue, Roboto, Verdana, sans-serif;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Tahoma;
}

button {
  background-color: #989898;
  border-radius: 10px;
  padding: 10px;
}

.container {
  width: 80%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 15px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.thumb {
  height: 400px;
}

.gallery a {
  height: 552px;
  width: calc(33.3333% - 6.66667px);
}

.thumb img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.info {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

.search-form {
  margin-bottom: 40px;
}

